@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* Universal Css*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  height: 100vh;
}

#root {
  height: 100vh;
}

.default-css {
  border: none;
  background: white;
  outline: none;
  cursor: pointer;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: inherit;
  padding-top: 5rem;
  background-image: url(/betadesk/static/media/background.540cc743.png);
  background-position: center;
  background-repeat: no-repeat;
}

.logo-container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 30rem;
}

.logo {
  height: 5rem;
  width: 5rem;
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.line {
  height: 5rem;
  border: 2px #25c16f solid;
}

.logo-text {
  font-size: 2.5rem;
  font-weight: 400;
  color: #183247;
}

.welcome-text {
  font-size: 1.5rem;
  margin-top: 1rem;
  letter-spacing: 0.1rem;
  color: #4d4d4d;
}

.form {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  min-width: 15rem;
}

.label {
  font-size: 1.1rem;
  font-weight: 200;
  padding: 0.5rem 0;
}

.login-input {
  min-height: 2.5rem;
  min-width: 20rem;
  text-indent: 0.5rem;
}

.login-input:placeholder-shown {
  font-family: "Poppins", sans-serif;
  border: 1px solid #25c16f;
  outline: none;
}

.btn-login {
  background: #25c16f;
  color: #fff;
  font-size: 1rem;
  margin-top: 1rem;
  min-height: 2rem;
  letter-spacing: 0.1rem;
}

.login-demo {
  font-size: 0.9rem;
  text-align: center;
  font-weight: 300;
  margin-top: 0.5rem;
}

/* Logo spin animation */
@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Mobile responsive*/
@media only screen and (min-width: 320px) and (max-width: 480px) {
  .logo-container {
    width: 22rem;
  }
  .welcome-text {
    text-align: center;
  }
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  left: 4rem;
  background: transparent;
  width: calc(100% - 4rem);
  height: 4rem;
}

.nav-container h1 {
  margin-left: 0.5rem;
  color: #183247;
}

.nav-container-logout {
  display: flex;
  align-items: center;
  margin-right: 0.5rem;
  cursor: pointer;
}

.logout-btn {
  padding-left: 0.2rem;
  color: #183247;
  font-weight: 400;
  font-size: 0.9rem;
}

.side_panel-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #12344d;
  width: 4rem;
}

.side_panel-icons {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 20rem;
}

.side_panel-logo {
  width: 4rem;
  height: 4rem;
  background: #02b875;
}

.side_panel-icon {
  display: flex;
}

.side_panel-icon p {
  display: none;
}

.side_panel-icon:hover p {
  display: inline;
  position: absolute;
  z-index: 3;
  left: 4rem;
  background: #383838;
  color: white;
  font-size: 0.8rem;
  padding: 0.5rem;
  border-top-left-radius: 20px;
  border-top-right-radius: 5px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 5px;
}

.viewport-container {
  position: absolute;
  top: 4rem;
  left: 4rem;
  height: calc(100% - 4rem);
  width: calc(100% - 4rem);
  background: #cfd7df7e;
}

/* View Ticket CSS */
.view_ticket-container {
  display: flex;
  height: 100%;
}

.view_ticket-content {
  margin: 1rem 0 0 1rem;
  width: 65%;
  background: #fff;
  padding: 0 2rem 0;
  overflow: scroll;
}

.view_ticket-main {
  display: flex;
  align-items: center;
}

.view_ticket-subject {
  display: flex;
  align-items: center;
  width: 100%;
  padding-top: 1rem;
}

.view_ticket-subject p {
  font-size: 1.5rem;
  font-weight: 500;
  padding-left: 0.5rem;
  color: #183247;
}

.view_ticket-status {
  display: flex;
  align-items: center;
}

.view_ticket-status div {
  height: 10px;
  width: 10px;
  border-radius: 50%;
}

.view_ticket-status p {
  padding-left: 0.5rem;
  font-weight: 300;
  font-size: 0.9rem;
  color: #183247;
}

.view_ticket-delete {
  display: block;
}

.view_ticket-assign {
  display: flex;
  justify-content: space-evenly;
  width: 75%;
  padding-bottom: 1rem;
}

.view_ticket-assign p {
  font-size: 0.8rem;
  font-weight: 300;
  color: #183247;
}

.view_ticket-box {
  border: 1px solid #70b2f3;
  background-color: #bbdcfe;
  min-height: 40%;
}

.view_ticket-box {
  color: #183247;
  font-size: 0.9rem;
  font-weight: 300;
}

.view_ticket-line {
  border: 1px solid #70b2f3;
}

.view_ticket-name {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  background: rgb(150, 211, 58);
  height: 50px;
  width: 50px;
  opacity: 0.7;
}

.view_ticket-box1 {
  display: flex;
  align-items: center;
  padding: 1rem;
}

.view_ticket-box1 p {
  padding-left: 0.5rem;
  color: #183247;
}

.view_ticket-box2 {
  padding: 1rem;
  max-height: 10rem;
}

/* Discussions Css */
.discussion-container {
  margin-top: 1rem;
  background-color: #88d1b693;
  border: 1px solid #02b875;
}

.discussion-container hr {
  border: 1px solid #02b875;
}

.discussion-title {
  display: flex;
  align-items: center;
}

.discussion-title:first-child {
  padding: 0.5rem;
}

.discussion-title h1 {
  padding: 0.5rem;
  font-weight: 400;
}

.discussion-message {
  max-height: 10rem;
  padding-left: 0.5rem;
  overflow: scroll;
}

.discussion-person {
  display: flex;
  align-items: center;
  padding: 0.5rem 0;
}

.discussion-details p {
  font-size: 0.9rem;
  font-weight: 300;
  padding-left: 0.5rem;
  color: #183247;
}

.discussion-user {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(150, 211, 58);
  color: white;
  height: 40px;
  width: 40px;
  opacity: 0.7;
}

.discussion-addnote {
  display: flex;
}

.discussion-addnote textarea {
  flex-grow: 1;
  text-indent: 0.5rem;
  outline: none;
}

.discussion-btn {
  background-color: #264966;
  color: white;
  font-size: 1rem;
  padding: 0.5rem;
}

/* Update Container */
.update_container {
  display: flex;
  width: 35%;
  margin-top: 1rem;
  height: calc(100% - 1rem);
}

.update_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 90%;
  background-color: #fff;
}

.view_ticket-delete {
  display: flex;
  align-items: center;
  position: absolute;
  top: 4rem;
  margin-left: -4.5rem;
}

.view_ticket-delete svg {
  position: absolute;
}

.view_ticket-delete p {
  font-weight: 400;
  font-size: 0.7rem;
}

.view_ticket-btn {
  border: 2px solid red;
  background: rgb(245, 84, 84);
  padding: 0.2rem 1rem;
  color: #fff;
}

.update_title {
  display: flex;
  align-items: center;
}

.update_title h1 {
  font-size: 1.2rem;
  color: #183247;
  font-weight: 400;
  margin-left: 0.5rem;
}

.update-btn {
  background-color: #264966;
  color: #fff;
  margin-top: 1rem;
  padding: 1rem 8.5rem;
}

.ticket_list-container {
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.ticket_list-width {
  width: 70%;
}

.ticket_list-content {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: scroll;
}

.ticket_list-box {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  background: white;
  box-shadow: 0 1px 1px 2px rgba(0, 0, 0, 0.05);
  padding: 1rem 0;
  margin: 0.5rem 0 0 0.5rem;
  border-left: 4px solid rgb(150, 211, 58);
  border-radius: 3px;
}

.ticket_list-name {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  background: rgb(150, 211, 58);
  height: 50px;
  width: 50px;
  opacity: 0.7;
}

.ticket_list-line {
  height: 4rem;
  border: 1px solid;
}

.ticket_list-detail {
  display: flex;
  flex-direction: column;
  min-width: 55%;
}

.ticket_list-from {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  flex-shrink: 1;
}

.ticket_list-from p,
.ticket_list-from span {
  font-size: 0.9rem;
  padding-left: 0.5rem;
  color: #6f7c87;
}

.ticket_list-group {
  display: flex;
  align-items: center;
}

.ticket_list-group p {
  padding-left: 0.5rem;
  font-size: 0.8rem;
  font-weight: 300;
  color: #183247;
}

.ticket_list-click {
  font-size: 0.7rem;
  margin-left: 0.2rem;
}

.ticket_list-date {
  font-size: 0.8rem !important;
  color: #1e2523 !important;
  font-weight: 200;
}

.ticket_list-status {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.1rem 0;
}

.ticket_list-status p {
  padding-left: 0.5rem;
  font-size: 0.8rem;
  font-weight: 400;
  color: #183247;
}

.ticket_list-a a {
  text-decoration: none;
  font-size: 1.1rem;
  color: #183247;
  word-break: break-all;
  transition: color 100ms linear;
}

.ticket_list-a a:hover {
  color: #02b875;
}

.ticket_list-priority {
  background: red;
  height: 16px;
  width: 16px;
  border-radius: 2px;
  transform: scale(0.6);
}

.ticket_list-filter {
  display: flex;
  flex-direction: column;
  position: fixed;
  left: calc(100% - 26%);
  top: 5rem;
  width: 25%;
}

.ticket_list-main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ticket_list_title {
  display: flex;
  align-items: center;
}

.ticket_list_title h1 {
  font-weight: 400;
  margin-left: 0.5rem;
  color: #183247;
}

.ticket_list-btn {
  display: flex;
  justify-content: space-between;
}

.filter-btn {
  background-color: #264966;
  color: #fff;
  margin-top: 0.5rem;
  padding: 0.5rem 1rem;
}

.filter-btn:last-child {
  margin-left: 0.5rem;
}

.create_ticket-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.create_ticket-field {
  display: flex;
  flex-direction: column;
  width: calc(100% - 4rem);
  padding: 0.4rem 0;
}

.create_ticket-field label {
  font-size: 0.8rem;
  padding-bottom: 0.1rem;
}

.ticket-field {
  border: 1px solid #38383869;
  padding: 0.5rem 0;
  border-radius: 5px;
  transition: border 100ms linear;
  text-indent: 5px;
}

.ticket-field:focus {
  border: 1px solid #02b875;
}

.ticket-btn {
  background: #264966;
  color: white;
  padding: 1rem 0;
  font-size: 1rem;
  font-weight: 300;
}

.overview_overall {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-image: url(/betadesk/static/media/overview.4ce91f61.png);
  background-size: contain;
}

.overview_total {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
}

.overview_count {
  width: calc(100% / 4.5);
  background-color: #fff;
  border-radius: 3px;
  margin: 1rem 0.5rem 0;
  padding: 1rem 2rem;
  box-shadow: 0 0 3px 0 #cfd7df7e;
  border: 4px solid #25c16f;
}

.overview_count span {
  font-size: 1.5rem;
}

.overview_chart {
  height: 100%;
  display: flex;
  align-items: center;
  width: 90%;
  background: #fff;
  margin: 3rem auto 0;
  border: 4px solid #25c16f;
}

.overview_bar {
  width: 80%;
  height: 80%;
}

.overview_available {
  display: flex;
  flex-direction: column;
  width: 20%;
  margin-left: 1rem;
  background: #fff;
}

.overview_agent {
  display: flex;
  align-items: center;
  padding: 1rem 0;
}

.overview_agent h1 {
  font-weight: 400;
  margin-left: 0.5rem;
}

.dashboard-container {
  height: inherit;
}

.split-view {
  display: flex;
  height: inherit;
}

